    "description": "You place a psychological compulsion in the mind of a creature within range that causes mental anguish if it performs a certain action. The creature must be able to hear and understand you. The target may make a Wisdom saving throw to resist the compulsion. On a failed save the target suffers 4d8 psychic damage and is subject to a compulsion for the duration. On a successful save the target suffers half damage and the spell ends. Choose one of the following compulsions when you cast the spell: Forbiddance. If the target leaves or enters the confines of a certain area you designate when you cast the spell it suffers 4d8 psychic damage at the end of each turn it spends outside that area. Pacifist. If the target attacks or directly inflicts damage on a living creature during a turn, it suffers 4d8 psychic damage at the end of that turn. Spellbane. If the target attempts to cast a spell during the duration, it suffers 4d8 psychic damage. It must make a must make a Constitution saving throw to maintain to concentration on the spell it is casting. The DC equals 10 or half the damage, whichever number is higher. On a failure the spell fails, it does not expend a spell slot or the use of the spell, but it still uses the action in the attempt. Truthful. If the target knowingly tells an untruth during the duration, it suffers 4d8 psychic damage. When a target suffers damage from violating the prohibited action, before the end of its next turn it may use its Action to attempt to break the spell by making a Wisdom saving throw versus your spell save DC. On a failed save the target suffers 4d8 psychic damage. On a successful save the target suffers half damage and the spell ends.",
