    "description": "As a Reaction when you or a creature you can see within range would take damage, or as a Bonus Action on your turn, you create an invisible protective barrier as a 10-foot radius hemisphere oriented as you wish either adjacent to you or centered upon you or a 25-foot square plane within 10 feet of you. Creatures on your side of the barrier have complete cover against attacks originating from the other side. Attacks crossing the barrier towards you impact and may damage the barrier if the attack roll exceeds the barrier's AC. Creatures attempting to cross towards you are also impeded as if they encountered an invisible wall. The Arcane barrier is AC 13 and has 30 hit points when cast. Certain attacks or spells which trigger when they strike their target trigger when they strike the barrier instead. Ifthe barrier is destroyed by an area of effect attack any excess damage may affect targets beyond the barrier though the attack's damage is reduced by the amount of damage necessary to destroy the barrier. The barrier is a physical construct, it has no effect on spells or effects that have no physical manifestation or that inflict only psychic damage. Attacks from your side of the Arcane Barrier against targets on the other side are not impeded and creatures may pass through it from your side to the other at will. The barrier is briefly visible as a plane of bluish light at the point at which it is encountered by a creature, structure, or attack. You are immobile while the barrier is in place. You may use an Action or Bonus Action on your turn to change how the barrier is oriented, to change the barrier to or from a hemisphere or a plane, or to remove the barrier. If you remove the barrier you can reestablish the barrier with its current hit points at a new location as an Action or Bonus Action at any point during the duration.",
