    "description": "Arcane Pact is also known as Coven Rite or Coterie Bond, this spell is always cast as a ritual the first time it is formed, a separate ritual must be performed for each new member that joins the pact. As part of that ritual the member receives the tattoo or brand allowing the mystical connection. The pact mystically links up to 13 arcane spellcasters who participate in the ritual, thereafter it can be reestablished by any member casting the spell using spell slots or by all members participating in a ritual, Any member of the pact can maintain the spell. For the duration of the spell, members of the pact may aid the spellcraft of other members within 100 feet in the following ways: - As a Reaction, you may donate the spell slot to power a spell cast by another member. - As a Reaction, you may spend sorcery or arcana points to apply Metamagic to a spell cast by another member. You can use only one Metamagic option on a spell, unless otherwise noted. - As a Reaction, you can deliver a touch spell cast by another member or act as the origin point of a spell cast by another member. - As a Reaction, you may take over concentration on a spell another member is currently maintaining when that member loses concentration or voluntarily surrenders the spell to you. You assume control over the spell and may direct it as if you had cast it yourself after the end of the original caster's next turn. - All members of the pact gain advantage on Constitution saving throws to maintain concentration on a spell if another member is within 100 feet, and that member isn't incapacitated. - You can take the Help Action on your turn to aid another member's spellcasting. When you do so, the chosen caster's next spell gains one of the following benefits: Advantage on a single attack roll with that spell against a target you can see, a single target you can see has disadvantage on one saving throw against that spell, or that spell counts as being cast using a spell slot 1 level higher for the purposes of countering or dispelling it. + When you cast a beneficial spell targeting another member of the pact within 100 feet you are considered to be within range regardless of the spell's actual range. - You can cast spells with a range of self and a casting time of 1 Reaction (such as shield) to protect another member of the pact you can see within 100 feet when it is hit by an attack or suffers damage. You are always aware of the magical activities of all members within range. While you do not possess actual telepathy, you subconsciously know what spells are being cast or maintained by other members of the pact, you know when members enter or leave the pact, their location within the area, and you are aware if a member becomes unconscious, is reduced to 0 hit points, or dies.",
