    "description": "You create an area of intense, breathtaking, cold within a 40- foot cube centered on a point you choose within range. The area of the spell becomes icy and is difficult terrain. When a creature enters the spell's area for the first time on a turn or starts its turn there it must make a Constitution saving throw. On a failed save it suffers 3d10 cold damage and one level of exhaustion as the heat is drained from it. Any levels of exhaustion caused by this spell go away when the cold damage inflicted by the spell is completely healed. On a successful save a creature suffers half damage and does not suffer exhaustion. A creature resistant or immune to cold damage does not suffer exhaustion from this spell.",
