    "description": "Choose an undead creature that you can see within range. The target must succeed on an Intelligence saving throw. On a failure, the undead creature is incapacitated, its speed reduced to 0, and it cannot change shape or form. At the end of each of its turns, a target with an Intelligence score of 8 or greater can make another Intelligence saving throw. On a success, the spell ends on the target. A corporeal undead creature with an Intelligence score of 7 or less that is bound for the entire duration falls under your control for 24 hours. On each of your turns, you can use a Bonus Action to mentally command any creature you control with this spell if the creature is within 90 feet of you (if you control multiple creatures, you can command any or all of them at the same time, issuing the same command to each one). You decide what Action the creature will take and where it will move during its next turn, or you can issue a general command, such as to guard a particular chamber or corridor. If you issue no commands, the creature only defends itself against hostile creatures. Once given an order, the creature continues to follow it until its task is complete. The creature is under your control for 24 hours, after which it stops obeying any command you've given it. To maintain control of the creature for another 24 hours, you must cast this spell on the bound creature again before the current 24-hour period ends. This use of the spell reasserts your control over up to two creatures you have bound with this spell, rather than binding a new one.",
