    "description": "For the duration of the spell your unarmed strikes are considered magical and you inflict an additional 1d4 thunder damage when you hit a target within your reach with an unarmed strike, a melee attack that inflicts thunder or lightning damage, or you successfully shove a target. At any point during the duration you can expend the spell when you hit a target within your reach with an unarmed strike or a melee attack that inflicts thunder or lightning damage, or you successfully shove a target. When expended, the spell ends, and a powerful blast of thunderous energy strikes the target inflicting 2d8 thunder damage. A Large or smaller target must make a Strength saving throw when struck by this attack, on a failure it is pushed up to 30 feet and knocked prone. If this causes a target to strike a solid object or creature both the target and the struck object or creature suffers 3d6 bludgeoning damage and are knocked prone. On a successful save, the creature is pushed half the distance but not knocked prone and does not suffer additional damage for impacting an object or creature. A creature struck by a pushed target can make a Dexterity saving throw. If successfully it moves aside, and the pushed target continues past it.",
