    "description": "This spell sends creatures into an enchanted slumber. Creatures within up to 60 feet of a point you choose within range must make a Wisdom saving throw. Creatures with a hit point maximum of less than 30 automatically fail this saving throw and creatures with a hit point maximum of less than 60 have disadvantage on this saving throw. On a failed saving throw a creature falls unconscious until the spell ends, the sleeper takes damage, or someone uses an action to shake or slap the sleeper awake. Undead and creatures immune to being charmed aren't affected by this spell.",
