    "description": "You conjure a transparent cube of acidic ooze in a 40-foot cube centered on a point you choose within range. Any flames in the area are doused. When a creature enters the spell's area for the first time on a turn or starts its turn there, it suffers 5d8 acid damage and must make a Dexterity saving throw. On a successful save a creature can force its way through the ooze, for every 1 foot a creature moves through the area, it must spend 3 feet of movement. Creatures within the cube can be seen but have total cover and cannot breathe. On a failed save, a creature is engulfed. The engulfed creature is restrained. An engulfed creature can try to escape by using an action to make a Strength check versus your spell save DC. On a success, the creature is no longer restrained and can force its way through the cube as described above. A creature can also take an action to attempt to free a creature it can reach within the cube. Doing so requires a successful Strength check versus your spell save DC, and the creature making the attempt takes 5d8 acid damage if it is not also within the area. At the start of each of your turns, each creature and object within the area takes 10d8 acid damage. (Magic items are unaffected.) As a bonus action on your turn, you can cause the cube to move up to 10 feet. When the cube moves, any engulfed creatures move with it, and any creature that the cube moves over must make a Dexterity saving throw or be engulfed as described above. The area of the cube will expand or contract to fill available space, up to its maximum area.",
