    "description": "You attempt to freeze one target you can see within range solid. If the target is a creature, it must make a Constitution saving throw. On a failed save, it suffers 12d6 cold damage is restrained as it begins to freeze. On a successful save, the creature suffers half damage, and the spell ends. A creature restrained by this spell must make another Constitution saving throw at the end of each of its turns. On a failed save, the creature suffers 6d6 cold damage. On a successful save, it suffers half damage. If it successfully saves against this spell three times, the spell ends. If it fails its saves three times, it is frozen. While frozen the creature is incapacitated, it remains restrained, and it suffers 6d6 cold damage at the end of each of its turns for the remaining duration. The successes and failures don't need to be consecutive; keep track of both until the target collects three of a kind. A creature that is immune to cold damage automatically succeeds at the Constitution saving throw versus this spell. A creature Restrained or Incapacitated by this spell becomes Vulnerable to Bludgeoning damage. If killed by this spell, a creature is a frozen statue until it thaws.",
