    "description": "You evoke an aura of intense cold in a 30-foot cube within range. Each creature in the area when you cast the spell suffers 3d4 cold damage must succeed on a Constitution saving throw or be slowed by the cold for the duration. A slowed target's speed is halved, it takes a —2 penalty to AC and Dexterity saving throws, and it can't use reactions. On its turn, it can use either an action or a bonus action, not both. Regardless of the creature's abilities or magic items, it can't make more than one melee or ranged attack during its turn. If the creature attempts to cast a spell with a casting time of 1 action, roll a d20. On an 11 or higher, the spell doesn't take effect until the creature's next turn, and the creature must use its action on that turn to complete the spell. If it can't, the spell is wasted. A creature affected by this spell makes another Constitution saving throw at the end of each of its turns. On a successful save, the effect ends for it. A creature resistant to cold damage has advantage on saving throws versus this spell. A creature immune to cold damage automatically succeeds on its saving throw.",
