    "description": "You cause a weapon you are wielding to grow in size. The weapon must have the two-handed or versatile property to be affected by this spell. For the duration of this spell the weapon is considered magical and you double the weapon's bludgeoning, piercing, or slashing damage dice to determine damage on a hit (e.g. A longsword would inflict 2d8 damage, or 2d10 wielded two handed, while a maul would inflict 4d6 damage on a hit). Only the weapon's base damage dice are affected. The magic of this spell allows you to wield the enlarged weapon without difficulty. The spell ends if the weapon leaves your gasp.",
    "higher_levels": "When you cast this spell using a spell slot of 3rd-level or higher, the duration increases by 1 minute per slot level above 2nd. When cast using a spell slot of 3rd or 4th-level the weapon also gains a +1 bonus to hit and damage, this increases to +2 using a 5th or 6th-level slot, or +3 using a 7th-level or higher slot. Arcane Synergy. This spell is one of what has been termed Metal Magic spells, developed by the Orders of Eldritch Knights within the Arcanum. This spell may be selected as a spell known by an eldritch knight of any order, regardless of the normal restrictions on selecting spells based upon a school of magic.",
