    "description": "You weave a set of magical protections about a specific structure. A structure of up to 2500 square feet can be protected by a casting of this spell. Multiple castings can be tied together to protect a larger structure. The entire structure, as well as all windows, doors, and other entrances are protected by an Arcane Lock spell and gain resistance to all damage types. A knock spell can only bypass an Arcane Lock on the warded structure if the caster of the knock succeeds at an ability check using its spellcasting ability vs your spell save DC. You or anyone you designate is mentally notified if any creature other than those you have authorized enters the structure uninvited, or if the structure is damaged, or if an attempt is made to gain unauthorized entrance to the structure. You can change what creatures are authorized as an Action at any time. A Hearth Ward protecting an entire structure can only be dispelled if the underlying structure is materially destroyed. You can also use the Hearth Ward as the foundation for other abjuration spells: + Endure Elements. The interior of the structure remains at a constant temperature; the spell becomes permanent until dispelled. + Glyph of Warding. A glyph of warding can be attached to the wards in such a way that it triggers on attempts to damage the structure, pick a lock, the casting of a knock spell, etc. An object inscribed with a glyph within the ward can me moved around freely as long as it does not leave the warded area. (It can also be set to trigger if removed from the warded area.) ¢ Magic Circle. A magic circle can be tied to a Hearth Ward in such a way to prohibit entry one type of creature described in the spell. If cast in such a way, the magic circle protects the structure and anyone within from such a creature as described in the spell and requires 500gp in components but becomes permanent until the structure is destroyed. This can be done multiple times, each casting hedging out one listed type of creature. +  Nondetection. Can be set to trigger if an unauthorized creature attempts to scry or use divination magic at a target within the structure, the spell then lasts for 24 hours. You or anyone you designate is alerted if this occurs. If cast on a warded structure every day for 30 days, the effects of a Nondetection spell can be made permanent on a structure. + Teleport Block. Can be set to trigger if an unauthorized creature attempts to teleport into the structure, it then lasts for 24 hours. The caster of the Hearth Ward is alerted if this occurs. + Private Sanctum. This spell can be tied to a structure protected by a Hearth Ward. If cast on a warded structure every day for 30 days, the effects of a Private Sanctum can be made permanent on the structure. You or anyone you designate can use an Action to change what properties of the sanctum are active at any time. -  Forbiddance. Can be made permanent on a structure protected by a Hearth Ward with a single casting. ¢ Guards and Wards: Can be set to trigger under certain conditions, the spell then lasts for up to 7 days. ¢ Symbol. A symbol can be attached to the wards in such a way that it triggers on attempts to damage the structure, pick a lock, the casting of a knock spell, etc. An object inscribed with a symbol within the ward can me moved around freely as long as it does not leave the warded area. (It can also be set to trigger if removed from the warded area.)",
