    "description": "You may use this spell to enact subtle illusions about a person or object. + Disguise. You enhance a disguise with a bit of illusion. You can use Minor Glamour as you would a disguise kit. It can make minor changes in appearance, changing color and style of hair, tint of skin, add or remove the appearance of dirt or cosmetics, slight alterations to the appearance of clothing and style (but not type) of armor, etc. You may use your spellcasting ability modifier on rolls to attempt a disguise, and if used in conjunction with a disguise kit with which you have proficiency, you have advantage on rolls to construct a disguise. You can also cast this this spell on an object to make it appear as a different object of the same general size and shape. + _ Enhance/Tarnish. You generally enhance or degrade the appearance of a willing creature or an object. Used to enhance, a creature's skin appears flawless, hair is styled and glistens, teeth sparkle, boots shine, etc. Used to tarnish, a creature's person and clothing appears dirty and unkempt. If cast on an object it appears particularly well made and kept or neglected and shoddy. - Attire. You can clothe a willing creature you touch in ethereal illusory garments. The garments are obviously magical and often include illusory magical effects such as shimmering translucent flame, strategically positioned concealing vapor, etc. Depending upon the effect, it can shed light, up to bright light in a 5-foot radius and dim light for an additional 5 feet. If cast upon yourself, you can change or renew the effect as a bonus action during the duration. ¢ Image. You create a small three-dimensional image at a point within your reach for as long as you concentrate. The image is accurate, but faintly translucent so cannot be mistaken for real for more than an instant. This effect is usually used to convey information, to provide amusement, or to play pranks. This effect can also generate light equal to a candle. You must concentrate to maintain the image. * Obscure. You subtly obscure the features of a creature or object. The effect is not obvious at the time, but the subtle illusion makes it difficult to recognize the affected target or to later recall its features. A suspicious creature can use its Action to make an Intelligence (Investigation) check versus your spell save DC to attempt to scrutinize a target it can see clearly. If successful, the spell no longer conceals the target's features from that creature. ¢  Veiling. You can render an object you touch that is no greater than 5 pounds invisible, it becomes visible if used in any manner. You can also cast this on your familiar to render it invisible until it attacks or casts a spell. You must concentrate to maintain this effect. If you cast this spell multiple times, you may have up to three effects that do not require concentration active a time, though no more than one effect can be active on any single creature or object. You gain the ability to maintain an additional effect when you reach 5th-level (4 effects), 11th-level (5 effects), and 17th-level (6 effects). Arcane Synergy. If you have the Improved Minor Illusion wizard class feature, the Fey Presence warlock class feature, or the Glamour sorcerer class feature you can have up to two effects active at once upon a creature or object.",
