import { writable } from 'svelte/store';

// selected spells
const storedSpells = window.localStorage.getItem('spellbook');
const defaultSpells = eval(`(${storedSpells})`) || [];
export const spellbook = writable(defaultSpells);
spellbook.subscribe(value => {
  window.localStorage.setItem('spellbook', JSON.stringify(value));
});

// sorting
export const sortField = writable('level');
export const sortDirection = writable('ASC');

// filtering
export const classes = writable(['Bard', 'Cleric', 'Druid', 'Paladin', 'Ranger', 'Sorcerer', 'Warlock', 'Wizard']);